<template>
  <div class="all-announcements-page">
    <div class="page-header">
      <h2> </h2>
      <p>       </p>
    </div>

    <div class="page-content">
      <AnnouncementsList
        :announcements="allAnnouncements"
        :totalPages="totalPages"
        :totalCount="totalCount"
        :loading="loading"
        :show-filters="true"
        :show-stats="true"
        :show-create-button="false"
        :show-edit-button="false"
        :show-pagination="true"
        :initialPage="currentPage + 1"
        :savedFilters="filters"
        @apply-filters="handleApplyFilters"
        @clear-filters="handleClearFilters"
        @page-changed="handlePageChange"
      />
    </div>
  </div>
</template>

<script>
import AnnouncementsList from '@/components/AllAnnouncementsList.vue'

export default {
  name: 'AllAnnouncements',
  components: {
    AnnouncementsList
  },
  data() {
    return {
      loading: false,
      allAnnouncements: [],
      totalPages: 0,
      totalCount: 0,
      filters: {},
      currentPage: 0,
      pageSize: 9
    }
  },
  mounted() {
    //    localStorage
    this.restoreState()
    //       
    this.loadAllAnnouncements(this.currentPage, this.filters)
  },
  methods: {
    async loadAllAnnouncements(currentPage, filters = {}) {
      this.loading = true
      this.currentPage = currentPage

      try {
        //  requestBody
        const requestBody = {}

        //    
        if (filters.title) {
          requestBody.title = filters.title
          requestBody.description = filters.title
        }

        if (filters.place && filters.place.city) {
          requestBody.place = {
            city: filters.place.city
          }
        }

        if (filters.completed !== undefined) {
          requestBody.completed = filters.completed
        }

        if (filters.type !== undefined) {
          requestBody.type = filters.type
        }

        if (filters.date) {
          requestBody.date = filters.date
        }

        if (filters.createdAt) {
          requestBody.createdAt = filters.createdAt
        }

        if (filters.hasFee !== undefined) {
          requestBody.hasFee = filters.hasFee
        }

        //  query 
        const queryParams = new URLSearchParams()
        queryParams.append('page', currentPage)
        queryParams.append('size', this.pageSize)
        queryParams.append('sort', 'createdAt,desc')

        const url = `/api/things?${queryParams.toString()}`
        const userId = localStorage.getItem('currentUserId')

        console.log('   :', requestBody)

        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': userId
          },
          body: JSON.stringify(requestBody)
        })

        if (response.ok) {
          const data = await response.json()
          this.allAnnouncements = data.items
          this.totalCount = data.total
          this.totalPages = Math.ceil(this.totalCount / this.pageSize)
          console.log(`  ${currentPage + 1}, : ${this.allAnnouncements.length}`)
        } else {
          console.error(' :', response.status)
        }
      } catch (error) {
        console.error(' :', error)
      } finally {
        this.loading = false
        //     
        this.saveState()
      }
    },

    handleApplyFilters(filters) {
      this.filters = filters
      this.currentPage = 0
      console.log(' :', filters)
      this.loadAllAnnouncements(this.currentPage, filters)
      this.saveState()
    },

    handleClearFilters() {
      this.filters = {}
      this.currentPage = 0
      console.log('     ')
      this.loadAllAnnouncements(this.currentPage, this.filters)
      this.saveState()
    },

    handlePageChange(page) {
      console.log('  :', page)
      //     API ( API     0)
      const apiPage = page - 1
      this.currentPage = apiPage
      this.loadAllAnnouncements(apiPage, this.filters)
      this.saveState()
    },

    //     localStorage
    saveState() {
      try {
        const state = {
          filters: this.filters,
          currentPage: this.currentPage,
          //     ,  
          timestamp: new Date().getTime()
        }
        localStorage.setItem('allAnnouncementsState', JSON.stringify(state))
        console.log(' :', state)
      } catch (error) {
        console.error('  :', error)
      }
    },

    //    localStorage
    restoreState() {
      try {
        const savedState = localStorage.getItem('allAnnouncementsState')

        if (savedState) {
          const state = JSON.parse(savedState)

          //  ,   
          if (state.filters && Object.keys(state.filters).length > 0) {
            this.filters = state.filters
            console.log(' :', this.filters)
          } else {
            //   ,    
            this.filters = {
              title: '',
              place: {
                city: ''
              }
            }
          }

          //   
          if (state.currentPage !== undefined && state.currentPage !== null) {
            this.currentPage = state.currentPage
            console.log(' :', this.currentPage + 1)
          }

          console.log('  :', state)
        } else {
          //    ,    
          this.filters = {
            title: '',
            place: {
              city: ''
            }
          }
          this.currentPage = 0
          console.log('   ')
        }
      } catch (error) {
        console.error('  :', error)
        //       
        this.filters = {
          title: '',
          place: {
            city: ''
          }
        }
        this.currentPage = 0
      }
    }
  },
  //      ,  
  beforeDestroy() {
    //        , :
    // localStorage.removeItem('allAnnouncementsState')
  }
}
</script>

<style scoped>
.all-announcements-page {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 100vh;
}

.page-header {
  text-align: center;
  margin-bottom: 3rem;
}

.page-header h2 {
  color: #8B5CF6;
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.page-header p {
  color: #666;
  font-size: 1.1rem;
}

.page-content {
  background: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
}

@media (max-width: 768px) {
  .all-announcements-page {
    padding: 1rem;
  }

  .page-header h2 {
    font-size: 2rem;
  }

  .page-content {
    padding: 1rem;
  }
}
</style>